import React, { useState, useEffect } from "react";
import { Header } from "../components";
import { ArrowDownIcon } from "../svg";
import axios from "axios";
import { ToastContainer, toast } from 'react-toastify';
const AddNewUser = () => {
  const [dropRole, setDropRole] = useState(false);
  const [selectedRole, setSelectedRole] = useState("Admin");
  const [username,setusername] = useState('')
  const [email,setemail] = useState('')
  const [password,setpassword] = useState('')
  const [confirmpassword,setconfirmpassword] = useState('')
  const [roles, setRoles] = useState([
    { label: "Admin" },
    { label: "User" },
    { label: "Superadmin" },
  ]);

  useEffect(() => {
    document.body.addEventListener("click", () => setDropRole(false));
  }, []);

  const Adduser = async () => {
    if(!password || !confirmpassword || !username || !email){
      toast("Please fill all fields",{
        theme : 'dark'
      });
    }
    else if(password != confirmpassword){
      toast("Password fields did'nt matched",{
        theme : 'dark'
      });
    }
    try {
      const res = await axios.post(`${process.env.REACT_APP_END_URL}/api/register`,{
        Username : username,Email : email,Password : password,Confirm_Pass : confirmpassword,usertype : selectedRole.toLowerCase()
    }); 
      console.log(res.data)
      if(res.data){
        toast(`${res.data.message}`,{
          theme : 'dark'
        });
      }
      setemail('')
      setusername('')
      setpassword('')
      setconfirmpassword('')
    } catch (error) {
      console.log(error.response) 
      if(error.response.data) {
          toast(`${error.response.data.error}`,{
            theme : 'dark'
          });
      }
    }
}

  return (
    <>
      <Header title="User Managment" hideRightbar={true} />
      <ToastContainer />
      <div className="add-new-user-p sidebar-gap">
        <div className="wrap flex flex-col aic">
          <div className="title-blk flex aic">
            <div className="title font s20 b6 c000 upc">Add New User</div>
          </div>
          <div className="form flex flex-col">
            <div className="cleanbtn feild flex flex-col">
              <div className="lbl font s14 c000">Username</div>
              <input 
              onChange={(e) => setusername(e.target.value)}
              value={username}
              className="iput font s14" />
            </div>
            <div className="cleanbtn feild flex flex-col">
              <div className="lbl font s14 c000">Email</div>
              <input 
               onChange={(e) => setemail(e.target.value)}
               value={email}
              className="iput font s14" />
            </div>
            <div className="cleanbtn feild flex flex-col">
              <div className="lbl font s14 c000">Password</div>
              <input 
              type="password"
              onChange={(e) => setpassword(e.target.value)}
              value={password}
              className="iput font s14" />
            </div>
            <div className="cleanbtn feild flex flex-col">
              <div className="lbl font s14 c000">Confirm Password</div>
              <input 
              type="password"
              onChange={(e) => setconfirmpassword(e.target.value)}
              value={confirmpassword}
              className="iput font s14" />
            </div>
            <div className="cleanbtn feild flex flex-col">
              <div className="lbl font s14 c000">Role</div>
              {/* Role Button */}
              <button
                className="cleanbtn iput flex aic font s14 c000 cstm-slt rel"
                onClick={(e) => {
                  e.stopPropagation();
                  setDropRole(!dropRole);
                }}
              >
                {roles.map(
                  (item) =>
                    selectedRole === item.label && (
                      <div className="lb wordwrap">{item.label}</div>
                    )
                )}
                {dropRole && (
                  <div className="options flex flex-col abs">
                    {roles.map((item) => (
                      <button
                        className="cleanbtn item flex aic anim"
                        onClick={() => {
                          setDropRole(!dropRole);
                          setSelectedRole(item.label);
                        }}
                      >
                        <div className="txt font s14 c000 wordwrap">
                          {item.label}
                        </div>
                      </button>
                    ))}
                  </div>
                )}
                <div className="ico s12 anim">
                  <ArrowDownIcon />
                </div>
              </button>
            </div>
            <div className="feild flex flex-col">
              <button
              onClick={() => Adduser()}
              className="btn cleanbtn font s14 cfff b4">Create</button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default AddNewUser;
