import {generateID} from '../../core/index'
import axios from 'axios';
const initState = {
  showRightbar: false,
  showSidebar: false,
  token : null,
// localStorage.getItem('myjwttoken'),
  userinfo : {},
  isAuthenticated : null,
  loaded : false,
  devicesinfo : {},
  orderloaded  : false,
  ws : null,
  leftdrawerstatus : false,
  activities : [],
  chats : [],
  messages : [],
  selecteduserid : '',
  recieveduserid : '',
  bellicon : false,
  updateuserchat : ''
};

const generalReducers = (state = initState, action) => {
  const { type, payload } = action;
  switch (type) {
    case "SHOW_RIGHT_BAR":
      return { ...state, showRightbar: payload };
    case "SHOW_SIDE_BAR":
      return { ...state, showSidebar: payload };
    case 'LOGIN_SUCCESS' : 
      {
         // localStorage.setItem('myjwttoken', action.payload.token)
      return {
          ...state,
          //isAuthenticated : true,
          token : action.payload.token,
          userinfo : action.payload.userlogin,
          isAuthenticated : true
      }
      }
    case 'DRAWER_LEFT' : 
         return {...state,leftdrawerstatus : action.payload}
    case 'UPDATE_DEVICES_INFO' : {
      return {
      ...state,
      devicesinfo : action.payload,
      }
    }
    case 'UPDATE_ACTIVITES' : {
      return {
        ...state,
        activities : action.payload
      }
    }
    case 'UPDATE_MESSAGES' : {
       return {
         ...state,
         messages : action.payload
       }
    }
    case 'UPDATE_USER_MESSAGES' : {
      console.log('update first',state.selecteduserid != action.payload.user)
      if(state.selecteduserid != action.payload.user){
        console.log('update in ifff')
        const updatestatus = async () => {
          await axios.post(
            `${process.env.REACT_APP_END_URL}/api/updatereadstatus`, {
              id : action.payload.user
            }
          );
        } 
        updatestatus()
        return {
          ...state,
          updateuserchat : generateID(8,7)
        }
      }
      else {
        console.log('update in else')
      return {
        ...state,
        messages : [...state.messages,action.payload]
      }
    }
   }
   case 'BELL_ICON' : {
    return {
      ...state,
      bellicon : action.payload
    }
 }
    case 'SELECTED_MESSAGE_USER_ID' : {
      return {
        ...state,
        selecteduserid : action.payload
      }
    }
    case 'RECIEVED_USER_ID' : {
      return {
        ...state,
        recieveduserid : action.payload
      }
    }
    case 'UPDATE_DEVICES_FAN_UPDATE' : {
      console.log('stateupdated', {...state.devicesinfo,...action.payload})
      return {
      ...state,
      devicesinfo : {...state.devicesinfo,...action.payload},
      }
    }
    case 'UPDATE_WS' : {
      return {
      ...state,
      ws : action.payload,
      }
    }
    case 'GET_USER' : {
      return {
          ...state,
          user : action.payload.data,
          isAuthenticated : true,
          loaded : true,
        
      }
    }
    case "SupportChat" : {
      return {
        ...state,
        chats : action.payload
      }
    }
    case 'SIGNUP_FAIL' : {
      return {
          ...state,
          signup : false,
          // we can put error here too if neccessary
          error : action.payload
      }
    }
    case 'LOGIN_FAIL' :
    case 'AUTHENTICATION_FAIL' :  {
      return {
          ...state,
          isAuthenticated : null,
          userinfo : null,
          loaded : true,
          token : null
      }
    }
    case 'USER_ORDERS'  : {
      return {
          ...state,
          orders : action.payload,
          orderloaded : true
      }
    }
    default:
      return state;
  }
};

export default generalReducers;
